import { useState, useEffect, useCallback } from "react";
import { useParams, Link as RouterLink } from "react-router-dom";
import { Helmet } from "react-helmet-async";
import {
  Box,
  Breadcrumbs,
  Button,
  Container,
  Grid,
  Link,
  Typography,
} from "@material-ui/core";
import useSettings from "../../hooks/useSettings";
import gtm from "../../lib/gtm";
import Header_Design from "../Transport/Header_Design";
import { customAxios } from "../apis/Axios";
import ComplaintListData from "./ComplaintListData";

const Complainlist = () => {
  const { settings } = useSettings();
  const [rows, setrows] = useState([]);
  const [open, setOpen] = useState(false);
  const [refresh, setRefresh] = useState(false);
  useEffect(async () => {
    const data = await customAxios.getCall(`complains/complaints`);
    setrows(data);
  }, [open, refresh]);

  useEffect(() => {
    gtm.push({ event: "page_view" });
  }, []);

  return (
    <>
      <Helmet>
        <title>Dashboard: Complaints </title>
      </Helmet>
      <Box
        sx={{
          backgroundColor: "background.default",
          minHeight: "100%",
          py: 8,
        }}
      >
        <Container maxWidth={settings.compact ? "xl" : false}>
          <Header_Design
            rows={rows}
            open={open}
            setOpen={setOpen}
            text="Complaint"
            dashText="Complaint List"
          />
          <Box sx={{ mt: 3 }}>
            <ComplaintListData
              setRefresh={setRefresh}
              refresh={refresh}
              invoices={rows}
            />
          </Box>
        </Container>
      </Box>
    </>
  );
};

export default Complainlist;
